@import 'global';
@import 'bootstrap/scss/reboot';
@import 'bootstrap/scss/type';
@import 'bootstrap/scss/images';

// Paragraphs
//
// Markup: paragraphs.twig
//
// Style guide: 2.paragraphs

p {
  max-width: $line-length;
}

.body--xxl {
  font-size: $text-xl;
  line-height: 1.6;
  font-weight: $font-weight-normal;

  @include media-breakpoint-up(lg) {
    font-size: $text-xxl;
    line-height: 1.4;
  }
}

.body--xl {
  font-size: $text-l;
  line-height: 1.6;
  font-weight: $font-weight-normal;

  @include media-breakpoint-up(lg) {
    font-size: $text-xl;
  }
}

.lede,
.body--l {
  font-size: $text-m;
  line-height: 1.6;
  font-weight: $font-weight-normal;

  @include media-breakpoint-up(lg) {
    font-size: $text-l;
  }
}

p,
ul,
ol,
.body--m {
  font-size: $text-s;
  line-height: 1.6;
  font-weight: $font-weight-normal;

  @include media-breakpoint-up(lg) {
    font-size: $text-m;
  }
}

.small,
.body--s {
  font-size: $text-xs;
  line-height: 1.6;
  font-weight: $font-weight-normal;

  @include media-breakpoint-up(lg) {
    font-size: $text-s;
  }
}

.body--xs {
  font-size: $text-xxs;
  line-height: 1.6;
  font-weight: $font-weight-normal;

  @include media-breakpoint-up(lg) {
    font-size: $text-xs;
  }
}

.body--xxs {
  font-size: $text-xxs;
  line-height: 1.6;
  font-weight: $font-weight-normal;
}

// Heading levels
//
// Markup: heading-levels.twig
//
// Style guide: 2.heading-levels

h1,
.heading--xxl {
  font-size: $heading-l;
  line-height: 1.1;
  margin-bottom: $spacer-m;

  @include media-breakpoint-up(lg) {
    font-size: $heading-xxl;
  }

  &:first-child {
    margin-top: 0;
  }
}

.heading--xl {
  font-size: $heading-l;

  @include media-breakpoint-up(lg) {
    font-size: $heading-xl;
  }
}

h2,
.heading--l {
  font-size: $heading-m;

  @include media-breakpoint-up(lg) {
    font-size: $heading-l;
  }
}

h3,
.heading--m {
  font-size: $heading-s;

  @include media-breakpoint-up(lg) {
    font-size: $heading-m;
  }
}

h4,
.heading--s {
  font-size: $heading-xs;

  @include media-breakpoint-up(lg) {
    font-size: $heading-s;
  }
}

h5,
.heading--xs {
  font-size: $heading-xs;
}

h6,
.heading--xxs {
  font-size: $heading-xxs;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  max-width: $line-length;
}

// Make sure heading classes match defaults.
.heading--xl,
.heading--l,
.heading--m,
.heading--s,
.heading--xs,
.heading--xxs {
  line-height: $headings-line-height;
  font-weight: $font-weight-bold;
  margin-top: 0;
  margin-bottom: $headings-margin-bottom;
  max-width: $line-length;
}

.subtitle {
  text-transform: uppercase;
  font-weight: $font-weight-bold;
  font-size: $heading-xxs;
  line-height: 1;
  margin-bottom: $spacer-xs;
  color: $text-muted;
}

// Bold/Italic
//
// Markup: bold-italic.twig
//
// Style guide: 2.bold-italic

b,
strong,
.font-weight-bold {
  font-weight: $font-weight-bold;
}

.font-weight-light {
  font-weight: $font-weight-normal;
}

// Link
//
// :hover - Hover
// :focus - Focus
// :active - Active
//
// Markup: link.twig
//
// Style guide: 2.link

a {
  transition: color .2s ease-in-out, border-color .2s ease-in-out;
  position: relative;

  &:active {
    top: -1px;
  }
}

.link-wrapper a,
.link {
  padding-bottom: 1px;
  border-bottom: $border-width solid;

  &:hover {
    color: $link-hover-color;
    padding-bottom: 0;
    border-bottom-width: 2px;
  }
}

h2 a,
h3 a,
h4 a {
  border-bottom: 2px solid transparent;

  &:hover {
    border-color: $link-hover-color;
  }
}

// External Link
//
// Markup: link-external.twig
//
// Style guide: 2.link-external

[href^='http']:not([href*='dpe.nsw.gov.au']) {
  @include icon('external', 12px);
  background-position: 100% 50%;
  padding-right: 16px;

  // sass-lint:disable no-vendor-prefixes
  @media screen and (-ms-high-contrast: active) {
    background-image: url('../images/external-white.svg');
  }

  @media screen and (-ms-high-contrast: black-on-white) {
    background-image: url('../images/external-black.svg');
  }

  @media print {
    background-image: none;
    padding-right: 0;
  }

  &::after {
    @media print {
      content: url('../images/external.svg');
      display: inline-block;
      width: 12px;
      height: 12px;
      margin-left: 4px;
    }
  }

  &.is-unstyled {
    background-image: none;
    padding-right: 0;

    &::after {
      @media print {
        display: none;
      }
    }
  }
}

// Unordered list
//
// Markup: unordered-list.twig
//
// Style guide: 2.list-unordered

// Ordered list
//
// Markup: ordered-list.twig
//
// Style guide: 2.list-ordered

ul,
ol {
  li {
    max-width: $line-length;
  }
}

// Table
//
// .table-bordered - Borders
// .table-striped - Zebra striped
//
// Markup: table.twig
//
// Style guide: 2.table

@import 'bootstrap/scss/tables';

table {
  table-layout: fixed;
}

// Horizontal rule
//
// Markup: horizontal-rule.twig
//
// Style guide: 2.horizontal-rule

// Blockquote
//
// Markup: blockquote.twig
//
// Style guide: 2.blockquote

blockquote {
  p {
    font-size: inherit;
  }

  // sass-lint:disable no-descending-specificity
  & :first-child {
    margin-top: 0;
  }

  & :last-child {
    margin-bottom: 0;
  }
  // sass-lint:enable no-descending-specificity
}

.blockquote {
  @include media-breakpoint-up(lg) {
    padding-left: $spacer-l;
  }
  padding-left: $spacer-m;
  border-left: 4px solid $primary;
  margin-bottom: $paragraph-margin-bottom;
}

@import 'bootstrap/scss/utilities/text';

// Centered text
//
// Markup: text-centered.twig
//
// Style guide: 2.centered

.text-center {
  &,
  p,
  ul,
  ol {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    max-width: $line-length;
  }

  li {
    list-style-position: inside;
  }
}

// Reset
.reset--button {
  border: 0;
  background: 0;
  padding: 0;
  cursor: pointer;
}

.reset--list {
  @include list-unstyled;
}

// Inline list
//
// Markup: inline-list.twig
//
// Style guide: 2.list-inline

.list-inline {
  display: flex;
  flex-wrap: wrap;

  li {
    &:not(:last-child) {
      margin-right: $list-inline-padding;
    }
  }
}

// Row list
//
// Markup: row-list.twig
//
// Style guide: 2.list-row

.list-row {
  padding-left: 0;
  list-style: none;
  border-top: $border-style;

  &-item,
  li {
    border-bottom: $border-style;
    padding-top: $spacer-sm;
    padding-bottom: $spacer-sm;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: none;

    > :last-child {
      text-align: right;
    }
  }
}

// Youtube video
//
// Markup: video.twig
//
// Style guide: 2.video

@import 'bootstrap/scss/utilities/embed';

.embed-responsive {
  margin: 0 0 $paragraph-margin-bottom;
}

// Figure
//
// Markup: figure.twig
//
// Style guide: 2.figure

.figure {
  display: table;
  margin-bottom: $paragraph-margin-bottom;
}

.figure-caption {
  padding-bottom: $spacer-s;
  border-bottom: $border-style;
}

.background-alt {
  background-color: $background-alt;

  h2 {
    color: $primary;
  }
}

// Skip to content link
//
// Markup: skip-link.twig
//
// Style guide: 2.skip-link

.skip-link {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  word-wrap: normal;

  &:active,
  &:focus {
    top: 0;
    left: 0;
    background: $background;
    clip: auto;
    overflow: visible;
    height: auto;
    width: auto;
  }

  &.focus-visible {
    outline-offset: 0;
  }
}

@import 'bootstrap/scss/utilities/screenreaders';
@import 'bootstrap/scss/utilities/visibility';

// Drupal classes for the screenreader util.
.visually-hidden,
%visually-hidden {
  @include sr-only;

  &--off,
  &.off {
    position: static;
    width: auto;
    height: auto;
    overflow: visible;
    clip: auto;
    white-space: normal;
  }

  &--focusable,
  &.focusable {
    @include sr-only;
    @include sr-only-focusable;
  }
}

@import 'bootstrap/scss/transitions';
